.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background-color: rgb(164, 142, 255);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15%;
}
.contianer{
  padding: 10px;
  border: 2px solid whitesmoke;
  width: 500px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  color: rgb(255,255,255);
}
.head{
  font-size: 20px;
  font-weight: 600;
  margin-bottom : 20px;
}

input{
  padding: 10px;
  border: 1px solid;
  border-radius: 10px;
}

input:hover{
  background-color:rgb(149, 149, 211);
}
input:focus{
  background-color: peachpuff;
  border: 0px;
}